.App {
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
}

h1 {
    text-align: center;
    font-size: xxx-large;
}

h2 {
    text-align: center;
    font-size: xx-large;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.section {
    width: 1000px;
    padding-top: 20px;
    padding-bottom: 50px;
    background-color: lavender;
    margin: auto;
    text-align: center;
}

.header {
    width: 624px;
    background-color: bisque;
    /* border: 1px solid brown; */
    margin: 0;
    padding: 10px;
}